e.data.result === 0) {\n                    this.show = false;\n                    this.$dispatch('edit-extra-amount');\n                } else {\n                    callback.success(response.data);\n                }\n                this.buttonEnabled = true;\n            },(response) => {\n                callback.error(response);\n                this.buttonEnabled = true;\n            });\n        },\n    \tcloseDialog() {\n            this.show = false;\n        },\n    }\n}\n</script>\n\n<style scoped>\n.modal {\n    height: 100%;\n}\n\n.modal-dialog {\n    top: 30%;\n}\n\n</style>"],"sourceRoot":"webpack://"}]);

